import React, { Fragment } from "react";
import { Link, Route } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import "../../App.css";
import Search from "./Search";
import { logoutUser } from "../../actions/userActions";
import SpinLoader from "../../components/layout/SpinLoader";
import { useAlert } from "react-alert";

const Header = () => {
  const { loading, user, isAuthenticated } = useSelector(
    (state) => state.authReducer
  );
  const { cartLoading, cartItemsLength } = useSelector(
    (state) => state.cartReducer
  );
  const dispatch = useDispatch();

  const alert = useAlert();

  const logoutHandler = () => {
    dispatch(logoutUser());
    alert.success("Logged out successfullly");
  };

  let userDropdownOptions = user && user.role === "admin" && (
    <Link className='dropdown-item' to='/dashboard'>
      Dashboard
    </Link>
  );

  let navigationOptions = user ? (
    <div className='ml-4 dropdown d-inline'>
      <Link
        to='/'
        className='btn dropdown-toggle text-white mr-4'
        type='button'
        id='dropDownMenu'
        data-toggle='dropdown'
        aria-haspopup='true'
        aria-expanded='false'>
        <figure className='avatar avatar-nav'>
          <img
            src={user.avatar && user.avatar.url}
            alt={user && user.name}
            className='rounded-circle'
          />
        </figure>
        <span>{user && user.name}</span>
      </Link>

      <div className='dropdown-menu' aria-labelledby='dropDownMenu'>
        {userDropdownOptions}
        <Link className='dropdown-item' to='/orders'>
          Orders
        </Link>
        <Link className='dropdown-item' to='/me'>
          Profile
        </Link>
        <Link
          to='/'
          className='dropdown-item text-danger'
          onClick={logoutHandler}>
          Logout
        </Link>
      </div>
    </div>
  ) : (
    !loading && (
      <Link to='/login' className='btn ml-4' id='login_btn'>
        Login
      </Link>
    )
  );

  const cart = isAuthenticated && (
    <Link to='/cart' style={{ textDecoration: "none" }}>
      <span id='cart' className='ml-3'>
        Cart
      </span>
      <span className='ml-1' id='cart_count'>
        {cartLoading ? <SpinLoader /> : cartItemsLength}
      </span>
    </Link>
  );

  return (
    <Fragment>
      <nav className='navbar row'>
        <div className='col-12 col-md-3'>
          <div className='navbar-brand'>
            <Link to='/'>
              <img src='/images/logo.png' alt='ShopIt' />
            </Link>
          </div>
        </div>

        <div className='col-12 col-md-6 mt-2 mt-md-0'>
          <Route render={({ history }) => <Search history={history} />} />
        </div>

        <div className='col-12 col-md-3 mt-4 mt-md-0 text-center'>
          {cart}
          {navigationOptions}
        </div>
      </nav>
    </Fragment>
  );
};

export default Header;
